import {
  Box,
  Flex,
  HStack,
  IconButton,
  useDisclosure,
  Stack,
} from "@chakra-ui/react";
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons";
import style from "../Styles/Navbar.module.css";
import resume from "../resume/Rahul-Kumar-Mishra-Resume.pdf";

export default function Navbar() {
  const { isOpen, onOpen, onClose } = useDisclosure();

  const handleResume = () => {
    window.open(
      "https://drive.google.com/file/d/1DoSowad6G171ACJ3dw63Z8z6TWztbbEm/view?usp=share_link"
    );
  };
  return (
    <>
    

      <Box
        style={{ width: "100%", 
        backgroundColor: "#393e46", 
        zIndex: "2",
        border:"0px solid #fff",
        display:"flex",
        alignItems:"center",
        justifyContent:"space-between"
       }}
       
        id="nav-menu"
        position="sticky"
        top={"0px"}
        margin={"auto"}
        width={"100%"}
      >

          <div className={style.logoText}  style={{
                  fontSize: "23px",
                  fontFamily: "Lobster Two cursive",
                  fontWeight: "800",
                  color: "#DCD7A0 ",
                }}>
            <h1 >RAHUL <span style={{background:'orange', color: 'rgb(117, 20, 20)', padding: '5px'}} >MISHRA</span></h1>
          </div>
         

        <Flex
          h={16}
          alignItems={"right"}
          justifyContent={"right"}
          position="relative"
        >

          

          <IconButton
            size={"md"}
            variant={"ghost"}
            color="#fff"
            icon={
              isOpen ? (
                <CloseIcon w={5} h={5} />
              ) : (
                <HamburgerIcon w={12} h={16} />
              )
            }
            aria-label={"Open Menu"}
            display={{ md: "none" }}
            onClick={isOpen ? onClose : onOpen}
            position={"relative"}
            left="30px"
            zIndex={444444}
          />
          <HStack spacing={23} alignItems={"center"}>
            <HStack
              as={"nav"}
              spacing={9}
              display={{ base: "none", md: "flex" }}
              color="#fff"
              fontSize="19px"
            >

          
              <a
                className="nav-link about"
                href="#home"
                style={{
                  fontSize: "23px",
                  fontFamily: "Lobster Two cursive",
                  fontWeight: "800",
                  color: "#DCD7A0 ",
                }}
              >
                Home
              </a>

              <a className="nav-link home" href="#about" style={{
                  fontSize: "23px",
                  fontFamily: "Lobster Two cursive",
                  fontWeight: "800",
                  color: "#DCD7A0 ",
                }}>
                About
              </a>
              <a className="nav-link skills" href="#skills" style={{
                  fontSize: "23px",
                  fontFamily: "Lobster Two cursive",
                  fontWeight: "800",
                  color: "#DCD7A0 ",
                }}>
                Skills
              </a>

              <a className="nav-link projects" href="#projects" style={{
                  fontSize: "23px",
                  fontFamily: "Lobster Two cursive",
                  fontWeight: "800",
                  color: "#DCD7A0 ",
                }}>
                Projects
              </a>
         
              <a   href="#contact" style={{
                  fontSize: "23px",
                  fontFamily: "Lobster Two cursive",
                  fontWeight: "800",
                  color: "#DCD7A0 ",
                }}>
               Contact
              </a>
             
              <button
                id="resume-button-1"
                onClick={handleResume}
                style={{ borderRadius: "12px 20px" }}
              >
                <a
                  id="resume-link-1"
                  href={resume}
                  download="Rahul-Kumar-Mishra-Resume"
                
                  className={`${style.resumes} nav-link resume`}
                  rel="noreferrer"
                  style={{
                    fontSize: "23px",
                    fontFamily: "Lobster Two cursive",
                    fontWeight: "800",
                
                  }}
                >
                  Resume
                </a>
              </button>
            </HStack>
          </HStack>
        </Flex>

        {isOpen ? (
          <Flex
            position="relative"
            left="230px"
            border={"2px solid orange"}
            bg={"#171717"}
            justifyContent={"right"}
            alignItems={"center"}
            padding="10px 34px"
            width={"40%"}
            height="auto"
            // borderRadius={"20px"}
          >
            <Box pb={1} display={{ md: "none" }}>
              <Stack as={"nav"} spacing={4} color="#fff">
                <a href="#home">Home</a>
                <a href="#about">About</a>
                <a href="#skills">Skills</a>
                <a href="#projects">Projects</a>
                <a href="#contact">Contact</a>
              </Stack>
            </Box>
          </Flex>
        ) : null}
      </Box>
    </>
  );
}
